@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont,Montserrat, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 25px;
}
h1 {
  font-size: 80px;
}
.App{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: white;
}

.App .search{
  text-align: center;
  padding: 10px;

}
.App input{
  font-family: Montserrat;
  padding:10px 20px ;
  font-size: 12px;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255,0.8);
  background: rgba(255,255,255,0.05);
  color: #ffffff;
}


::placeholder{
  color: white;
}


.App:before{
  content: '';
  background: url("./assets/background.jpeg") no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.container{
  max-width: 700px;
  height: 700px;
  margin: auto;
  padding: 0 5px;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.App .top{
  width: 100%;
  margin: 10px auto;

}

.App .description{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(270deg);
}

.App .bottom{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  padding: 10px;
  border-radius: 12px;
  background-color: rgba(255,255,255,0.2);
}
.bold{
  font-weight: 700;
}
